.navigationContainer {
    display: flex;
    background-color: lightsalmon;
    width: 100%;
    width: calc(100% - 10px);
    margin: 5px;
    flex-direction: column;
}

.navigationContainerHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 5px;
}

.navigationItem {
    display: flex;
    flex-direction: column;
    margin: 5px;
    padding: 5px;
    height: 200px;
}

.navigationItemHeader {
    font-size: 25px;
    font-weight: 500;
}

.loadingContainer {
    width: 100%;
    height: 3px;
    background-color: #f1f1f1;
    position: relative;
    overflow: hidden;
}

.loadingBar {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    background-color: #4CAF50;
    animation: loading-bar-animation 5s linear infinite;
    animation-play-state: running;
}

@keyframes loading-bar-animation {
    0% {
        left: -100%;
        width: 100%;
    }
    100% {
        left: 100%;
        width: 100%;
    }
}

.loadingBarPaused {
    animation-play-state: paused;
}

.playPauseIconContainer {
    cursor: pointer;
}

.playPauseIcon {
    width: 10px;
    height: 10px;
    margin: 5px;
    padding: 5px;
    background-color: lightgreen;
    border-radius: 5px;
}