.scrollListSelectorContainer {
    display: flex;
    flex-direction: column;
    overflow-y: auto;
}

.scrollListSelectorItem {
    display: flex;
    background-color: aliceblue;
    padding: 10px;
    cursor: pointer;
    width: 200px;
}

.scrollListSelectorItem:hover {
    background-color: blue; /* New text color on hover */
}

.scrollListSelectorItemActive {
    background-color: lightblue;
}