.videMeetingItem {
    display: flex;
    flex-direction: column;
    background-color: lightblue;
    margin: 1px;
    padding: 10px;
    border-radius: 10px;
    cursor: pointer;
    border: 2px solid lightblue;
}
.videMeetingItemActive {
    background-color: rgb(175, 233, 233);
    border: 2px solid rgb(175, 233, 233);
}

.videMeetingItemSelected {
    border: 2px solid black;
}

.videMeetingItemTitle {
    display: flex;
    margin: 4px;
}

.videMeetingItemDate {
    display: flex;
    margin: 4px;
}

.videMeetingItemDescription {
    display: flex;
    margin: 4px;
}

.videMeetingParticipants {
    display: flex;
    flex-direction: column;
    margin: 4px;
}